'use client'

import { ContentKind, ContentStatus } from '@/types'
import { Book, Video, FileText, ExternalLink, Star, Plus, Trash2 } from 'lucide-react'
import { useAppStore } from '@/store/useAppStore'
import { useState } from 'react'
import { NewItemDialog } from './NewItemDialog'

interface MainTableProps {
  selectedMonth: string
  selectedItem: string | null
  onItemSelect: (itemId: string | null) => void
}

export function MainTable({ selectedMonth, selectedItem, onItemSelect }: MainTableProps) {
  const items = useAppStore((state) => state.items)
  const itemDetails = useAppStore((state) => state.itemDetails)
  const deleteItem = useAppStore((state) => state.deleteItem)
  const [isNewItemDialogOpen, setIsNewItemDialogOpen] = useState(false)

  const getKindIcon = (kind: ContentKind) => {
    switch (kind) {
      case 'book':
        return <Book size={16} className="text-cyan-300" />
      case 'video':
        return <Video size={16} className="text-blue-300" />
      case 'article':
        return <FileText size={16} className="text-sky-300" />
    }
  }

  const getStatusBadge = (status: ContentStatus) => {
    const styles = {
      done: 'bg-cyan-900/70 text-cyan-100 border-cyan-600/50',
      in_progress: 'bg-blue-900/70 text-blue-100 border-blue-600/50',
      planned: 'bg-slate-800/70 text-slate-100 border-slate-600/50'
    }

    const labels = {
      done: '完了',
      in_progress: '進行中',
      planned: '予定'
    }

    return (
      <span className={`px-2 py-1 text-xs font-semibold rounded-md border ${styles[status]} backdrop-blur-sm`}>
        {labels[status]}
      </span>
    )
  }

  const renderRating = (rating?: number) => {
    if (!rating) return <span className="text-slate-500 text-sm font-medium">-</span>
    
    return (
      <div className="flex items-center gap-1">
        <Star size={12} className="text-yellow-300 fill-current" />
        <span className="text-sm font-bold text-white">{rating}/10</span>
      </div>
    )
  }

  const renderTags = (tags: string[]) => {
    const displayTags = tags.slice(0, 3)
    const remainingCount = Math.max(0, tags.length - 3)

    return (
      <div className="flex items-center gap-1 flex-wrap">
        {displayTags.map((tag, index) => (
          <span 
            key={index}
            className="px-2 py-0.5 text-xs bg-gradient-to-r from-cyan-900/60 to-blue-900/60 text-cyan-100 rounded border border-cyan-500/40 font-semibold backdrop-blur-sm"
          >
            {tag}
          </span>
        ))}
        {remainingCount > 0 && (
          <span className="px-2 py-0.5 text-xs bg-slate-800/60 text-slate-200 rounded border border-slate-600/40 font-semibold">
            +{remainingCount}
          </span>
        )}
      </div>
    )
  }

  const renderMemoIndicator = (itemId: string) => {
    const details = itemDetails[itemId]
    if (!details) return null
    
    const ideaCount = details.workIdeas.length
    const learningCount = details.learnings.length
    
    if (ideaCount === 0 && learningCount === 0) return null
    
    return (
      <div className="flex items-center gap-1">
        {ideaCount > 0 && (
          <span className="px-2 py-0.5 text-xs bg-cyan-900/70 text-cyan-100 rounded border border-cyan-600/50 font-bold">
            {ideaCount}
          </span>
        )}
        {learningCount > 0 && (
          <span className="px-2 py-0.5 text-xs bg-blue-900/70 text-blue-100 rounded border border-blue-600/50 font-bold">
            {learningCount}
          </span>
        )}
      </div>
    )
  }

  return (
    <div className="flex-1 flex flex-col bg-background">
      {/* Header */}
      <div className="p-6 border-b border-border">
        <div className="flex items-center justify-between mb-2">
          <h1 className="text-2xl font-bold">学習記録</h1>
          <button
            onClick={() => setIsNewItemDialogOpen(true)}
            className="flex items-center gap-2 px-4 py-2 gradient-accent text-white rounded-xl hover:opacity-90 transition-opacity"
          >
            <Plus size={16} />
            新規追加
          </button>
        </div>
        <p className="text-foreground opacity-70">
          {selectedMonth === 'uncategorized' ? '未分類のアイテム' : `${selectedMonth.replace('-', '年')}月の記録`}
        </p>
      </div>

      {/* Table Container */}
      <div className="flex-1 overflow-auto">
        <div className="min-w-full">
          {/* Table Body */}
          <div className="divide-y divide-border">
            {items.map((item) => (
              <div 
                key={item.id}
                className={`grid grid-cols-13 gap-3 px-4 py-3 hover:bg-card transition-colors group ${
                  selectedItem === item.id ? 'bg-card ring-2 ring-cyan-400 ring-opacity-30' : ''
                }`}
              >
                {/* Kind */}
                <div className="col-span-1 flex items-center">
                  <div className="flex items-center gap-2">
                    {getKindIcon(item.kind)}
                    {item.amazonUrl && (
                      <button 
                        className="text-orange-400 hover:text-orange-300 transition-colors"
                        onClick={(e) => {
                          e.stopPropagation()
                          window.open(item.amazonUrl, '_blank')
                        }}
                        title="Amazon"
                      >
                        <ExternalLink size={14} />
                      </button>
                    )}
                    {item.notebooklmUrl && (
                      <button 
                        className="text-green-400 hover:text-green-300 transition-colors"
                        onClick={(e) => {
                          e.stopPropagation()
                          window.open(item.notebooklmUrl, '_blank')
                        }}
                        title="NotebookLM"
                      >
                        <ExternalLink size={14} />
                      </button>
                    )}
                  </div>
                </div>

                {/* Title & Creators */}
                <div className="col-span-3">
                  <div className="font-bold text-white text-sm line-clamp-1 mb-0.5">
                    {item.title}
                  </div>
                  <div className="text-xs text-slate-300 font-medium">
                    {item.creators.join(', ')}
                  </div>
                </div>

                {/* Rating */}
                <div className="col-span-1 flex items-center">
                  {renderRating(item.rating)}
                </div>

                {/* One-liner */}
                <div className="col-span-3 flex items-center">
                  <p className="text-sm text-slate-200 font-medium line-clamp-1">
                    {item.oneLiner || '—'}
                  </p>
                </div>

                {/* Tags */}
                <div className="col-span-2 flex items-center">
                  {renderTags(item.tags)}
                </div>

                {/* Status */}
                <div className="col-span-1 flex items-center">
                  {getStatusBadge(item.status)}
                </div>

                {/* Memo Indicator */}
                <div className="col-span-1 flex items-center">
                  {renderMemoIndicator(item.id)}
                </div>

                {/* Actions */}
                <div className="col-span-1 flex items-center gap-2">
                  <button
                    onClick={(e) => {
                      e.stopPropagation()
                      onItemSelect(selectedItem === item.id ? null : item.id)
                    }}
                    className="p-1 hover:bg-cyan-900/50 rounded opacity-0 group-hover:opacity-100 transition-all"
                    title="詳細表示"
                  >
                    <ExternalLink size={12} className="text-cyan-300" />
                  </button>
                  <button
                    onClick={(e) => {
                      e.stopPropagation()
                      if (confirm(`「${item.title}」を削除しますか？`)) {
                        deleteItem(item.id)
                      }
                    }}
                    className="p-1 hover:bg-red-900/50 rounded text-red-300 hover:text-red-200 opacity-0 group-hover:opacity-100 transition-all"
                    title="削除"
                  >
                    <Trash2 size={12} />
                  </button>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>

      {/* New Item Dialog */}
      <NewItemDialog
        isOpen={isNewItemDialogOpen}
        onClose={() => setIsNewItemDialogOpen(false)}
      />
    </div>
  )
}